import selfTaskBox from '@/components/self-task-box.vue';
import selfInput from '@/components/self-input.vue';
import selfButtonRadio from '@/components/self-button-radio.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';
import selfCheckbox from '@/components/self-checkbox.vue';

export default {
	data() {
		return {
			detail: {},
			startTime: '',
			targetCom: '',
			chekck: false,
			arriveWays: ['A', 'B', 'C'],
			arriveWayIdx: 0,
		}
	},
	components: {
		selfTaskBox,
		selfInput,
		selfButtonRadio,
		selfButton,
		timer,
		selfCheckbox
	},
	onLoad(options) {
		const { detail } = options;
		if(detail) {
			this.detail = JSON.parse(detail);
			console.log('-----', this.detail)
		}
	},
	methods: {
		startTimeChange(e) {
			this.startTime = e
		},
		arriveWayChange(e) {
			this.arriveWayIdx = e.target.value
		},
		selectHandle() {
			this.chekck = !this.chekck
		},
		okClick() {
			alert('a')
		}
	}
}
